    font-size: 1.625em;
    font-weight: 600;
    letter-spacing: inherit;
    line-height: 1.25;
    text-transform: inherit;
    font-weight: 400;
  }
}

@media only all and (max-width: 37.5em) {
  .m-hero__image-wrapper {
    margin-top: 1.875em;
  }
  .m-hero--overlay .m-hero__wrapper {
    background-image: none !important;
  }
  .m-hero--jumbo .m-hero__wrapper {
    padding-bottom: 0;
  }
}
@media only all and (max-width: 56.25em) {
