    line-height: 1.25;
    text-transform: inherit;
  }
  .m-hero__subhead {
    font-size: 18px;
  }
}
@media only all and (min-width: 37.5625em) {
  .m-hero__wrapper {
    grid-template-columns: 7fr 5fr;
    padding-right: 15px;
    padding-left: 15px;
    min-height: 225px;
  }
  .m-hero__text {
    margin: auto;
  }
  .m-hero__image-wrapper {
    padding-right: 15px;
    padding-left: 15px;
    display: flex;
    align-items: center;
  }
  .m-hero--bleeding .m-hero__image-wrapper {
    width: 100%;
    margin-top: -2.8125em;
    margin-bottom: -2.8125em;
  }
  .m-hero--bleeding .m-hero__image {
    padding-bottom: 0 !important;
    height: 100%;
    width: 100%;
    background-size: cover;
  }
  .m-hero--overlay .m-hero__image {
    display: none;
  }
  .m-hero--jumbo .m-hero__wrapper {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .m-hero--jumbo .m-hero__image {
    display: none;
  }
  .m-hero--50-50 .m-hero__wrapper {
    grid-template-columns: 1fr 1fr;
    border: 1px solid var(--gray-40);
    border-top: none;
    background-position: 100% center;
    background-repeat: no-repeat;
    background-size: 50%;
  }
  .m-hero--50-50 .m-hero__image {
    display: none;
  }
}
@media only all and (min-width: 63.8125em) {
  .m-hero__wrapper {
    padding-top: 2.8125em;
    padding-bottom: 2.8125em;
    min-height: 195px;
  }
  .m-hero--bleeding .m-hero__image-wrapper {
    margin-top: -2.8125em;
    margin-bottom: -2.8125em;
  }
  .m-hero--50-50 .m-hero__wrapper {
    min-height: 345px;
  }
